
import Vue from 'vue'
export default {
	onLaunch: function() {
		this.connectWebSocket()
		this.bindSocket()
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
	methods: {
		connectWebSocket: function () {
			uni.connectSocket({
				url: this.globalData.socketUrl,
				success: () => {
					this.globalData.socketConnectStatus = true
					this.loginSocket()
				}
			})
		},
		bindSocket: function () {
			uni.onSocketClose(() => {
				this.globalData.socketConnectStatus = false
			})
		},
		reConnectSocket: function (msg, obj) {
			uni.closeSocket({
				complete: () => {
					uni.connectSocket({
						url: this.globalData.socketUrl,
						success: () => {
							this.globalData.socketConnectStatus = true
							this.loginSocket()
							this.sendSocket(msg, obj)
						}
					})
				}
			})
		},
		loginSocket: function () {
			if (this.globalData.code) {
				this.sendSocket("login", {code: this.globalData.code})
			}
		},
		sendSocket: function (msg, obj = "") {
			if (this.globalData.socketConnectStatus) {
				uni.sendSocketMessage({
					data: JSON.stringify({
						msg: msg,
						obj: obj
					})
				})
			} else {
				this.reConnectSocket(msg, obj)
			}
		},
		setCode: function (value) {
			this.globalData.code = value
			uni.setStorageSync("code", value)
		},
		getCode: function () {
			return uni.getStorageSync("code")
		}
	},
	globalData: {
		userInfo: null,
		userInfoServer: null,
		openid: null,
		phone: null,
		code: null,
		socketConnectStatus: false,
		requestUrl: "http://www.ljs.com/",
		socketUrl: "ws://127.0.0.1:8888"
	}
}
